import React from "react";

const Book = (props) => {
  const { img, title, author } = props;
  return (
    <article
      className="book"
      onMouseOver={() => {
        console.log(title);
      }}
    >
      <img src={img} alt="" />
      <h1>{title}</h1>
      <h4>{author}</h4>
      <button
        type="button"
        onClick={() => {
          alert("hello world");
        }}
      >
        {" "}
        Big Red Button
      </button>
    </article>
  );
};

export default Book;
